<template>
  <v-container>
    <v-layout
      align-center
      row
      justify-end
    >
      <v-toolbar dark extended color="primary">
        <v-toolbar-title class="white--text">Previous Location</v-toolbar-title>
        <v-layout
          align-center
          row
          justify-center
          slot="extension"
        >
          <v-flex xs5>
            <v-autocomplete
              v-model="previousCountry"
              :items="countries"
              :rules="[rules.required]"
              :error="previousCountryError"
              required
              validate-on-blur
              label="Previous Country"
            >
            </v-autocomplete>
          </v-flex>

          <v-flex xs5>
            <v-text-field
              v-model="previousCity"
              :rules="[rules.required]"
              :error="previousCityError"
              required
              validate-on-blur
              label="City"
            ></v-text-field>
          </v-flex>
        </v-layout>
      </v-toolbar>
      <v-toolbar dark extended color="primary">
        <v-toolbar-title class="white--text">Next Location</v-toolbar-title>
        <v-layout
          align-center
          row
          justify-center
          slot="extension"
        >
          <v-flex xs5>
            <v-autocomplete
              v-model="nextCountry"
              :items="countries"
              :rules="[rules.required]"
              :error="nextCountryError"
              validate-on-blur
              label="Next Country"
            >
            </v-autocomplete>
          </v-flex>
          <v-flex xs5>
            <v-text-field
              v-model="nextCity"
              :rules="[rules.required]"
              :error="nextCityError"
              validate-on-blur
              label="City"
            ></v-text-field>
          </v-flex>
        </v-layout>
      </v-toolbar>
    </v-layout>
    <v-layout
      align-center
      row
      justify-center
      v-if="showKey"
    >
      <v-textarea
        box
        label="Key Info"
        auto-grow
        readonly
        :value="reservation.keyInfo"
      ></v-textarea>
    </v-layout>
    <v-layout
      align-center
      row
      justify-center
    >
      <v-btn round color="primary" class="headline" @click.native="saveClicked">
        Save trip plan to complete Check-In
        <v-icon right dark>touch_app</v-icon>
      </v-btn>
    </v-layout>
  </v-container>
</template>

<script>
import * as CountryListModules from 'country-list'
import { mapState, mapGetters, mapActions } from 'vuex'
import { CallDialog, Exceptions } from '@/utils'
import store from '@/store'

export default {
  data() {
    return {
      previousCountry: null,
      previousCountryError: false,
      nextCountry: null,
      nextCountryError: false,
      previousCity: null,
      previousCityError: false,
      nextCity: null,
      nextCityError: false,
      rules: {
        required: value => !!value || 'Required.',
      },
      showKey: false,
    }
  },
  async beforeRouteEnter (to, from, next) {
    const error = await store.dispatch('checkIn/loadMembers').catch(error => {
      return error
    })

    if (error) {
      CallDialog(error)
    } else {
      // store.dispatch('checkIn/setCurrentStep', 1)
      next()
    }
  },
  async beforeRouteUpdate (to, from, next) {
    const error = await store.dispatch('checkIn/loadMembers').catch(error => {      
      return error
    })

    if (error) {
      CallDialog(error)
    } else {
      next()
    }
  },
  computed: {
    countries() {
      return CountryListModules.getNames()
    },
    width: {
      get() {
        // return document.documentElement.clientWidth
        return this.$window.width
      }
    },
    height: {
      get() {
        // return document.documentElement.clientHeight
        return this.$window.height
      }
    },
    ...mapState({
      members: state => state.checkIn.members,
      reservation: state => state.checkIn.reservation,
    }),
  },
  created() {
    console.log('key created')
    this.showKey = false
  },
  methods: {
    async saveClicked() {
      const anyError = this.validateInputs()
      if (anyError) {
        return
      }      
      
      const allIdMatches = !this.members.some(crtMember => !crtMember.idMatches)
      if (!allIdMatches) {
        return CallDialog(Exceptions.CustomNotAllIdsRecognized)
      }

      const allNamesSet = !this.members.some(crtMember => {
        if (!crtMember.fullName) {
          return true
        }
      })
      if (!allNamesSet) {
        return CallDialog(Exceptions.CustomNotAllNamesSet)
      }

      const error = await this.saveLocations({
        previousCountry: this.previousCountry,
        nextCountry: this.nextCountry,
        previousCity: this.previousCity,
        nextCity: this.nextCity
      }).catch(e => {
        console.error('saveMember', e)
        return e
      })

      if (error) {
        return CallDialog(error)
      } 

      this.showKey = true

    },
    validateInputs() {
      if (this.previousCountry) {
        this.previousCountryError = false
      } else {
        this.previousCountryError = true
      }

      if (this.nextCountry) {
        this.nextCountryError = false
      } else {
        this.nextCountryError = true
      }

      if (this.previousCity) {
        this.previousCityError = false
      } else {
        this.previousCityError = true
      }

      if (this.nextCity) {
        this.nextCityError = false
      } else {
        this.nextCityError = true
      }

      return this.nextCityError || this.previousCityError || this.previousCountryError || this.nextCountryError
    },
    ...mapActions({
      saveLocations: 'checkIn/saveLocations',
    })
  },

}
</script>
